import React from "react";
import CenterLayout from "../components/CenterLayout";
import injectSheet from "react-jss";
import Logo from "../assets/img/full_logo_transparent.png";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faAddressCard, faEnvelope, faPhone } from "@fortawesome/free-solid-svg-icons";

const styles = {
    footerStyle: {
        display: "block",
        minHeight: 300,
        background: "#2c5278",
        position: "relative",
        padding: "50px 0 80px 0",
    },
    footerWrapper: {
        display: "flex",
        flexDirection: "row",
        justifyContent: "space-between",
        padding: "0 200px",
        color: "#FFF",
    },
    companyInfo: {
        display: "flex",
        flexDirection: "column",
        width: 250,
        gap: 12,
        "& img.logo": {
            width: 160,
            borderRadius: 4,
            background: "#FFF",
            padding: "8px 10px",
        },
        "& .info": {
            fontSize: 14,
            lineHeight: 1.5,
            color: "#000000",
        }
    },
    contactInfo: {
        display: "flex",
        flexDirection: "column",
        width: 200,
        gap: 8,
        color: "#FFF",
        "& .title": {
            fontWeight: 700,
            fontSize: 20,
            marginBottom: 10,
        },
        "& .companyName": {
            fontWeight: 600,
            fontSize: 14,
        },
        "& .address": {
            fontSize: 14,
        },
        "& .contact": {
            display: "flex",
            alignItems: "flex-start",
            gap: 8,
            fontSize: 14,
        },
        "& .contactIcon": {
            color: "#FFF",
            position: "relative",
            top: 1,
        },
    },
}

const Footer = ({ classes }) => {
    return <div className={classes.footerStyle}>
        <CenterLayout>
            <div className={classes.footerWrapper}>
                <div className={classes.companyInfo}>
                    <img src={Logo} alt="App Logo" className="logo" />
                    <span>LSK SILNIKI ELEKTRYCZNE</span>
                </div>
                <div className={classes.contactInfo}>
                    <span className="title">Contact</span>
                    <span className="contact">
                        <FontAwesomeIcon icon={faAddressCard} className={"contactIcon"} />
                        Legnicka 142, Wroclaw, Poland 54-206
                    </span>
                    <span className="contact">
                        <FontAwesomeIcon icon={faPhone} className={"contactIcon"} />
                        +48 792757600
                    </span>
                    <span className="contact">
                        <FontAwesomeIcon icon={faEnvelope} className={"contactIcon"} />
                        info@silesiaelectricmotors.com
                    </span>
                </div>
            </div>
        </CenterLayout></div>;
}

export default injectSheet(styles)(Footer);